import React from 'react';
import Post from './MyPost/Post';
import s from './MyPosts.module.css'
import { Field, reduxForm } from "redux-form";
import { required, maxLengthCreator } from '../../../utilits/validators/validators';
import { Textarea } from '../../../common/FormsControls/FormsControls';


const MyPosts = React.memo(props => {
	let PostsItem = props.PostsData
		.map((p) => {
			return <Post id={p.id} message={p.message} likes={p.likes} />
		})


	let addNewPost = (values) => {
		props.addPost(values.newPostBody)
	}

	return <div className={s.posts}>
		<h2>My Posts</h2>
		<AddPostFormRedux onSubmit={addNewPost} />
		{PostsItem}
	</div>
}
)


const addPostForm = (props) => {
	return (
		<form onSubmit={props.handleSubmit}>
			<div>	<Field validate={[required, maxLengthCreator(10)]} component={Textarea} name={"newPostBody"} placeholder={"Enter new post"} /></div>
			<div><button className={s.button}  >add Post</button></div>
		</form>

	)
}

const AddPostFormRedux = reduxForm({ form: 'addNewPostForm' })(addPostForm)



export default MyPosts;