import React from 'react';
import s from './Post.module.scss'

const Post = (props) => {
	return (
		<div className={s.post}>
			<a href=""><img src="https://shapka-youtube.ru/wp-content/uploads/2022/03/lev-na-avatarku.jpg" alt="" /></a>
			<div className={s.textPost}>{props.message}</div>
			<div><button>like</button></div>
			<div className={s.textPost}>{props.likes}</div>

		</div>
	)

}

export default Post;