import React from 'react';
import Preloader from '../../../common/Preloader/Preloader';
import s from "./ContentProfile.module.scss"
import ContentStatus from './ContentStatus'
import ContentStatusWithHooks from './ContentStatusWithHooks'

const ContentProfile = (props) => {
	if (!props.profile) {
		return <Preloader />
	}
	return <div className={s.content}>
		{/* <a href="" className={s.image}><img src="https://klike.net/uploads/posts/2019-06/1559370682_9.jpg" alt="" /></a> */}
		<div className={s.contentBody}>
			<img className={s.avatar} src={props.profile.photos.large} alt="" />
		</div>
		<div className={s.description}>
			<div>{props.profile.fullName}</div>
			<ContentStatusWithHooks status={props.status} updateStatus={props.updateStatus} />
			{/* <div>статус: {props.profile.aboutMe}</div> */}
			{/* <div>{props.profile.fullName}</div>
			<div>{props.profile.fullName}</div>
			<div>{props.profile.fullName}</div> */}
		</div>
	</div>
}

export default ContentProfile;