import React from 'react';
import Content from './Content';
import s from './Content.module.scss'
import { connect } from 'react-redux';
import { setUsersProfile } from '../../Redux/ContentReducer'
import { useParams } from 'react-router-dom';
import { getProfile, getStatus, updateStatus } from '../../Redux/ContentReducer'
import { withAuthRedirect } from '../../hoc/WithAuthRedirect'


// let mapStateToProps = (State) => ({
// 	profile: State.ContentPage.profile,
// 	State: State.ContentPage.status
// })
let mapStateToProps = (State) => {
	return {
		profile: State.ContentPage.profile,
		status: State.ContentPage.status,
		authorizedUserId: State.Auth.Id,
		isAuth: State.Auth.isAuth
	}

}

class ContentContainer extends React.Component {

	componentDidMount() {
		let userId = this.props.params.userId
		if (!userId) {
			userId = 27287
			//this.props.authorizedUserId
			// if (!userId) {
			// 	this.props.history.push('/content')
			// }
		}
		this.props.getProfile(userId)
		this.props.getStatus(userId)
	}

	render() {
		return <div>
			<Content {...this.props} status={this.props.status} updateStatus={this.props.updateStatus} />
		</div>
	}
}
let AuthRedirectComponent = withAuthRedirect(ContentContainer)

const WithUrlDataContainerComponent = (props) => {
	return <AuthRedirectComponent {...props} params={useParams()} />
}

export default connect(mapStateToProps, { setUsersProfile, getProfile, getStatus, updateStatus })(WithUrlDataContainerComponent);	
